.film-collection {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.film-card {
  width: 280px;
}

.film-card__img {
  width: 280px;
  border-radius: 5px;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  position: fixed;
  top: 0;
  left: 0;
}

.modal-window {
  width: 300px;
  height: 100%;
  background-color: #f0f8ff;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

